<template>
  <a-menu
    :style="{ width: '100%' }"
    :selected-keys="selected_keys"
    @menu-item-click="new_chat_visible = true"
  >
    <a-menu-item key="1"> </a-menu-item>
  </a-menu>

  <a-drawer
    :width="600"
    :visible="new_chat_visible"
    @ok="handleOk"
    @cancel="new_chat_visible = false"
    unmountOnClose
    :ok-loading="ok_loading"
    :hide-cancel="ok_loading"
  >
    <template #title></template>
    <a-form :model="form">
      <a-form-item field="chat_title" tooltip="" label="">
        <a-input v-model="form.chat_title" placeholder="" />
      </a-form-item>

      <a-form-item field="system" label="" tooltip="">
        <a-select v-model="form.system" placeholder="" allow-clear>
          <a-option value=""></a-option>
          <a-option value=""></a-option>
        </a-select>
      </a-form-item>

      <a-form-item
        field="chat_parameters.model"
        label=""
        tooltip=""
      >
        <a-select
          v-model="form.chat_parameters.model"
          placeholder=""
          allow-clear
          value-key="model_call"
          @change="handleChange"
        >
          <a-option
            v-for="item of UserStore.gettersModelList"
            :value="item"
            :label="item.model_name"
          />
        </a-select>
      </a-form-item>

      <a-form-item
        field="system"
        label=""
        tooltip=""
      >
        <a-slider
          v-model="form.chat_parameters.top_p"
          :min="0"
          :max="1.0"
          :step="0.1"
        />
      </a-form-item>

      <a-form-item
        field="system"
        label=""
        tooltip=""
      >
        <a-slider
          v-model="form.chat_parameters.temperature"
          :min="0.1"
          :max="1.9"
          :step="0.1"
        />
      </a-form-item>

      <!--      <a-form-item field="system" label="" tooltip="">-->
      <!--        <a-switch v-model="form.chat_parameters.enable_search">-->
      <!--          <template #checked>  </template>-->
      <!--          <template #unchecked>  </template>-->
      <!--        </a-switch>-->
      <!--      </a-form-item>-->
    </a-form>

    <a-descriptions
      v-show="descriptionsData.length"
      style="margin-top: 10px"
      :data="descriptionsData"
      title=""
      :column="1"
    />
  </a-drawer>
</template>

<script setup lang="ts">
import { onMounted, reactive, ref, watch } from "vue";
import { useUserStore } from "@/store";
import { Api } from "@/api/api";
import { Message } from "@arco-design/web-vue";

const UserStore = useUserStore();
const props = defineProps(["getData"]);

// 
const user_info = UserStore.user_info;

// 
const new_chat_visible = ref<boolean>(false);
// 
const selected_keys = ref<string[]>([""]);
// 
const ok_loading = ref<boolean>(false);

const descriptionsData = ref<
  {
    label: string;
    value: string;
  }[]
>([]);

// 
const form = reactive({
  user_id: user_info._id,
  user_name: user_info.username,
  chat_title: "",
  system: "",
  chat_parameters: {
    model: "",
    messages: [],
    top_p: 0.7,
    temperature: 0.7,
    presence_penalty: null,
    max_tokens: null,
    response_foramt: "text",
    seed: null,
    stream: false,
    stop: null,
    tools: null,
    stream_options: null,
    enable_search: false,
  },
});

const handleChange = (modelInfo: {}) => {
  form.chat_parameters.model = modelInfo.model_call;

  descriptionsData.value.push({
    label: "",
    value: modelInfo.model_call,
  });
  descriptionsData.value.push({
    label: "",
    value: modelInfo.model_introduction,
  });
  descriptionsData.value.push({
    label: "",
    value: modelInfo.limit,
  });
  descriptionsData.value.push({
    label: "",
    value: modelInfo.residue_limit,
  });
};

// 
const handleOk = async () => {
  ok_loading.value = true;
  if (form.chat_title === "") form.chat_title = "";

  try {
    const res = await Api.add_chat(form);

    if (res.status_code === 200) {
      Message.success(res.data.message);
      props.getData();
    } else {
      Message.error(res.message);
    }
  } catch (err) {
    console.log("");
  } finally {
    new_chat_visible.value = false;
    ok_loading.value = false;
    form.chat_title = "";
    form.system = "";
    form.chat_parameters = {
      model: "",
      messages: [],
      top_p: 0.7,
      temperature: 0.7,
      presence_penalty: null,
      max_tokens: null,
      response_foramt: "text",
      seed: null,
      stream: false,
      stop: null,
      tools: null,
      stream_options: null,
      enable_search: false,
    };
    descriptionsData.value = [];
  }
};

// 
watch(new_chat_visible, (new_value) => {
  if (new_value) {
    selected_keys.value = ["1"];
  } else {
    selected_keys.value = [""];
  }
});

// 
onMounted(async () => {
  if (UserStore.gettersModelList.length === 0) {
    await UserStore.getModelList();
  }
});
</script>

<style scoped></style>
