<template>
  <a-list :max-height="computedHeight" scrollbar :bordered="false">
    <template #header>
      {{ props.messageList.length === 0 ?  "" : props.messageList[0].content }}
    </template>
    <a-list-item v-for="(item, index) in props.messageList" :key="index">
      <a-list-item-meta :title="item.role"  v-if="item.role !== 'system'">
        <template #description>
            <MarkdownRenderer :markdown="item" />
        </template>
        <template #avatar>
          <a-avatar shape="square">
            <img
              alt="avatar"
              src="https://p1-arco.byteimg.com/tos-cn-i-uwbnlip3yd/3ee5f13fb09879ecb5185e440cef6eb9.png~tplv-uwbnlip3yd-webp.webp"
            />
          </a-avatar>
        </template>
      </a-list-item-meta>
      <template #actions>
        <icon-edit />
        <icon-delete />
      </template>
    </a-list-item>
  </a-list>
</template>
<script setup lang="ts">
import { computed, onBeforeUnmount, defineProps, ref } from "vue";
import MarkdownRenderer from "@/components/chat/content/MarkdownRenderer.vue";

const props = defineProps({
  messageList: {
    type: Array,
    required: true,
  },
});

/*  */
// 
const max_height = ref(window.innerHeight);

// 
const updateWindowHeight = () => {
  max_height.value = window.innerHeight;
};

// 
window.addEventListener("resize", updateWindowHeight);
updateWindowHeight();

// 
onBeforeUnmount(() => {
  window.removeEventListener("resize", updateWindowHeight);
});

// 161 footerHeight
const computedHeight = computed(() => {
  return max_height.value - 64 - 162;
});
</script>
<style scoped></style>
